const createDiv = () => document.createElement('div');

const createP = () => document.createElement('p');

const div = createDiv();

div.setAttribute('class', 'normal-div')

const placeDiv = () => document.body.appendChild(div);


const placeText = (txt, div) => {
    const p = createP();
    p.innerText = txt;
    p.setAttribute('title', 'Tem coisa estranha ai')

    div.appendChild(p);
};

placeDiv();

for (let i = 1; i<101; i++) {
    placeText(`${i} vamos mais uma vez`, div);
};

